html,
body {
  background: yellow;
  height: 100%;
  margin: 0;
  overflow: hidden;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

h1 {
  position: absolute;
  left: calc(50% - 150px);
  top: calc(25% - 100px);
  width: 300px;
  height: 200px;
  letter-spacing: -1.5px;
  line-height: 34px;
  color: #ffd636;
  font-size: 2.5em;
}
h2 {
  letter-spacing: -1.5px;
  line-height: 21px;
  color: #ffd636;
}

.Title {
  position: absolute;
  left: calc(50% - 150px);
  top: calc(73% - 10px);
  width: 300px;
}
.Info {
  background: none;
  outline: none;
  border: none;
  color: white;
  font-size: 0.9em;
  font-weight: 700;
  background: red;
  border-radius: 30px;
  margin-top: 5px;
}
.App {
  position: absolute;
  top: 0;
  min-height: 100vh;
  min-width: 100vw;
  font-family: sans-serif;
  text-align: center;
  background: green;
}

.Copyright {
  position: absolute;
  left: calc(50% - 100px);
  width: 200px;
  bottom: 20px;
  background: none;
  outline: none;
  border: none;
  color: white;
  font-size: 10px;
}

.Paperella {
  position: absolute;
  left: calc(50% - 100px);
  top: calc(50% - 100px);
  width: 200px;
  height: 200px;
  display: flex;
  overflow: hidden;
  background: red;
  border-radius: 100%;
}

.PaperellaLink {
  width: 100%;
  height: 100%;
  display: flex;
  justify-items: center;
  align-content: center;
  background: none;
  outline: none;
  border: none;
  color: white;
}
.PaperellaImg {
  width: 100%;
  height: 100%;
}
